// src/App.js

import React, { useState } from "react";
import { Auth } from "./components/Auth"; // Ensure this path is correct based on your project structure
import "./Auth.css"; // Import your styles

function App() {
  const [isAuth, setIsAuth] = useState(false);

  return (
    <div className="App">
      {isAuth ? <p>Welcome! You are signed in.</p> : <Auth setIsAuth={setIsAuth} />}
    </div>
  );
}

export default App;
